.hello img {
  position: relative;
  width: 2rem;
  height: 2rem;
  display: inline-block;
}
.hello img:after {
  content: attr(alt) " Loading ...";
  display: block;
  box-sizing: border-box;
  padding-top: 1rem;
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #f5f5f5;
  text-align: center;
  font-size: 0.24rem;
  color: rgb(100, 100, 100);
